@use 'reset';
@import 'variables';
@import 'mixins';

/* 1. Main Layout ================================= */

* {
	box-sizing: border-box;
}

body {
	background: $textBLk;
}

/* 2. Content ================================= */

body {
	font-family: $BodyFont;
}

.anchor{
	position: relative;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-template-rows: repeat(4, 1fr);
	width: 6em;
	height: 6em;

	.ring{
		grid-area: 1/2/2/3;
		border: 5px solid $fill-color;
		border-radius: 100%;
		width: 69%;
		justify-self: center;
	}
	.shaft{
		width: 20%;
		height: 100%;
		background-color: $fill-color;
		display: flex;
		grid-area: 2/2/5/3;
		align-self: center;
		justify-self: center;
	}
	.crossShaft{
		width: 100%;
		height: 20%;
		background: $fill-color;
		grid-area: 2/2/3/3;
		align-self: flex-end;
	}
	.catch{
		border-radius: 100%;
		grid-area: 2/1/5/4;
		border-bottom: 7px solid $fill-color;
		justify-self: center;
		width: 70%;
	}
	.cleft{
		background: $fill-color;
		width: 20%;
		height: 50%;
		justify-self: center;
		align-self: flex-end;
	}
	.cleftL{
		grid-area: 3/1/4/2;
		transform: skewY(45deg);
	}
	.cleftR{
		transform: skewY(-45deg);
		grid-area: 3/3/4/4;
	}
}
.chainlink{
	margin: 4px 4px;
	border: 2px solid $fill-color;
	border-radius: 100%;
	width: 15px;
	height: 15px;
	position: relative;
	left: 10%;
	transition: rotate 42s ease-in-out;
}

.home_head{
	background: $primary-color;
	height: 13vh;
	border-bottom: .93em solid $grey-color;
	display: grid;
	grid-template-columns: repeat(4, 1fr) 18%;
	grid-template-rows: repeat(1, 1fr);
	z-index: 40;
	position: relative;

	.hide-small{
		display: none;
	}
	
	.logoLink{
		grid-column: 2/5;
		grid-row: 1/3;
		width: 100%;
		position: relative;
		z-index: 50;
		
		.head_logo{
			width: inherit;
			display: flex;
			justify-content: center;
			img{
				width: 69%;
			}
		}
		@include bp-SDuo{
			.head_logo{
				img{
					width: 50%;
				}
			}
		}
	}
	h1{
		grid-column: 2/5;
		grid-row: 1/2;
		color: $primary-color;
		font-size: 2em;
		align-self: center;
		font-family: $LobFont;
		text-transform: capitalize;
		justify-self: center;
		text-shadow: -1px -1px 0 $secondary-color, 1px -1px 0 $secondary-color, -1px 1px 0 $secondary-color, 1px 1px 0 $secondary-color;

		@include bp-M{
			font-size: 2.5em;
		}
	}

	.burg_btn{
		grid-column: 5/6;
		padding: 13px 20%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-self: center;
		height: fit-content;
		cursor: pointer;
		gap: 10px;

		div{
			background: $secondary-color;
			width: 100%;
			height: 9px;
			border-radius: 30px;
			border: .13em solid $accent-color;
		}
	}

	#closed{
		.topBun{
			transform: translateY(0px) rotate(0deg);
			transition: transform 420ms ease;
		}
		.botBun{
			transform: translateY(0px) rotate(0deg);
			transition: transform 420ms ease;
		}
	}

	#open{
		padding: 18px 20%;
		gap: 0%;
		.topBun{
			transform: translateY(5px) rotate(-495deg);
			transition: transform 420ms ease;
		}
		.botBun{
			transform: translateY(-5px) rotate(-405deg);
			transition: transform 420ms ease;
		}
	}
	#active{
		display: block;
		position: fixed;
		height: 95vh;
		top: 8%;
		width: 100%;
		background: $textBLk;
		z-index: 2;
		opacity: 0%;
		transition: opacity 420ms ease;

		ul{
			display: flex;
			flex-direction: column;
			height: 80%;
			justify-content: space-between;
			text-align: center;
			padding: 80px;
			position: relative;
			transform: translateX(-100%);
			transition: transform 420ms ease;

			a{
				text-decoration: none;
				color: $primary-color;
				text-shadow: -1px -1px 0 $secondary-color, 1px -1px 0 $secondary-color, -1px 1px 0 $secondary-color, 1px 1px 0 $secondary-color;
				font-family: $LobFont;
				font-size: 4.2em;
				text-transform: capitalize;
			}
		}
	}

	@include bp-M{
		grid-template-columns: repeat(6, 1fr) 15%;
		
		h1{
			grid-column: 3/6;
		}
		.burg_btn{
			grid-column: 7/8;
		}
	}

	@include bp-L{
		grid-template-columns: repeat(11, 1fr);
		z-index: 2;

		.logoLink{
			grid-column: 1/2;
			z-index: 2;
		}
		h1{
			grid-column: 2/5;
			font-size: 3em;
			justify-self: left;
		}
		.hide-small{
			grid-column: 8/12;
			display: flex;
			opacity: 100%;

			ul{
				display: flex;
				flex-direction: row;
				width: 100%;
				justify-content: space-between;
				align-items: center;
				padding: 0% 5%;

				a{
					text-decoration: none;
					text-transform: capitalize;
					font-family: $LobFont;
					color: $primary-color;
					font-size: 2em;
					text-shadow: -1px -1px 0 $secondary-color, 1px -1px 0 $secondary-color, -1px 1px 0 $secondary-color, 1px 1px 0 $secondary-color;
					transition: text-shadow 420ms ease-in-out;
				}
				a:hover{
					text-shadow: 0px 0px 1.3rem $secondary-color, 0px 0px 1.3rem $secondary-color, 0px 0px 1.3rem $secondary-color, 0px 0px 1.3rem $secondary-color;
				}
			}
		}
		.hide-large{
			display: none;
		}
	}
	@include bp-XL{
		.logoLink {
			position: absolute;
			width:fit-content;
			transition: box-shadow 4200ms ease-in-out;
			box-shadow: none;
			border-radius: 100%;
			.head_logo {
				background-color: $primary-color;
				border-radius: 50%;
				width: fit-content;
				border: .13em solid $accent-color;
				img{
					width: 85px;
				}
			}
		}
		.logoLink:hover{
			box-shadow: 0px 0px 1.3rem $secondary-color, 0px 0px 1.3rem $secondary-color, 0px 0px 1.3rem $secondary-color, 0px 0px 1.3rem $secondary-color;
			border-radius: 100%;
			transition: box-shadow 4200ms ease-in-out;
		}
	}
}


#fixed{
	position: fixed;
	width: 100%;
	z-index: 40;
	background: #333136;
}


.followTray{
	background: #333136e3;
	border-top: .13em solid #444247e7;
	position: fixed;
	width: 100%;
	height: 6vh;
	bottom: 0%;
	z-index: 40;

	ul{
		display: grid;
		grid-template-columns: minmax(1.3%, 7%) repeat(3, 1fr) minmax(1.3%, 7%);
		height: 100%;
		column-gap: 1.3em;

		li{
			display: flex;
			justify-content: center;
			align-items: center;
			padding: 4.2px 0%;

			a{
				height: 100%;
				width: 100%;
				border: .13em solid rgba(255, 217, 0, 0.822);
				border-radius: 50px;
				display: flex;
				justify-content: center;
				align-items: center;
				text-decoration: none;

				figure{
					display: flex;
					justify-content: center;

					i{
						font-size: 1.3em;
						color: $textGreyLite;
						text-decoration: none;

						@include bp-M{
							font-size: 2em;
						}
					}
				}
			}
		}

		.followTray_link1{
			grid-column: 2/3;
		}
		.followTray_link2{
			grid-column: 3/4;

			img{
				width: 30px;
			}
		}
		.followTray_link3{
			grid-column: 4/5;
		}
	}

	@include bp-L{
		background: #333136;
		left: 0%;
		width: fit-content;
		height: 100vh;
		display: flex;
		border-top: none;
		border-right: .13em solid #444247e7;
		z-index: 0;

		ul{
			grid-template-columns: 1.3px 1fr 1.3px;
			grid-template-rows: repeat(3, 1fr) minmax(1.3%, 7%);
			height: 33%;
			margin-top: auto;
			row-gap: 1em;
			column-gap: .42em;

			li{
				padding: 0%;
				width: 50px;
				height: 66.6px;
			}

			.followTray_link1{
				grid-area: 1 / 2 / 2 / 3;
			}
			.followTray_link2{
				grid-area: 2 / 2 / 3 / 3;
			}
			.followTray_link3{
				grid-area: 3 / 2 / 4 / 3;
			}
		}
	}
}

.sign{
	display: grid;
	grid-template-rows: repeat(3, 1fr);
	width: 13em;
	height: 5em;
	align-items: center;
	text-align: center;

	.plank{
		background-color: rgb(165, 77, 42);
		border: 5px solid black;
		width: 100%;
		height: 100%;
		border-radius: 10%;
		display: block;
		z-index: 30;
	}
	article{
		color: $secondary-color;
		text-transform: capitalize;
		font-family: $LobFont;
		padding: 0% 13%;
		font-size: 3.3em;
		position: absolute;
		opacity: 60%;
		z-index: 40;
		justify-content: center;
		text-shadow: -1px -1px 1rem $accent-color 1px -1px 1rem $accent-color -1px 1px 1rem $accent-color 1px 1px 1rem $accent-color;
	}
}

